@charset "UTF-8";

@font-face {
  font-family: "logger";
  src:url("fonts/logger.eot");
  src:url("fonts/logger.eot?#iefix") format("embedded-opentype"),
    url("fonts/logger.woff") format("woff"),
    url("fonts/logger.ttf") format("truetype"),
    url("fonts/logger.svg#logger") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "logger" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "logger" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close:before {
  content: "\61";
}
.icon-config:before {
  content: "\62";
}
.icon-info:before {
  content: "\63";
}
